.App {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}

.image-container {
  position: relative;
  width: 100%; /* Scale with browser */
  max-width: 1280px; /* Ensure it doesnâ€™t exceed original dimensions */
  aspect-ratio: 1280 / 427; /* Maintain the original aspect ratio */
  overflow: hidden;
  margin: 0 auto; /* Center horizontally */
}

.input-container input {
  margin: 10px 0;
  padding: 10px;
  font-size: 16px;
  border: 1px solid #ccc;
  border-radius: 4px;
}

.input-container button {
  padding: 10px;
  font-size: 16px;
  background-color: #007bff;
  color: white;
  border: none;
  border-radius: 4px;
  cursor: pointer;
}

.input-container button:hover {
  background-color: #0056b3;
}

.overlay-image {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 0;
  object-fit: contain;
}

.uploaded-image {
  width: 18%; /* Scale relative to the container */
  height: auto; /* Maintain aspect ratio */
  position: absolute;
  top: 14%; /* Adjust placement */
  left: 77.25%; /* Adjust placement */
  z-index: 1; /* Ensure it appears above the ticket */
}

.text-overlay {
  position: absolute;
  top: 76%; /* Adjust placement relative to the container */
  left: 86%; /* Adjust placement relative to the container */
  transform: translate(-50%, -50%); /* Ensure perfect centering */
  z-index: 2; /* Ensure it appears above the ticket */
  color: white; /* Text color */
  font-size: 2.5vw; /* Scale text size dynamically with viewport width */
  text-align: center;
}

.generated-image-container {
  margin-top: 20px;
  text-align: center;
}

.generated-image-container img {
  max-width: 100%; /* Scale with the container */
  border: 1px solid #ccc;
  padding: 10px;
  background-color: #f9f9f9;
}
